<template>
  <div class="register-page" :style="{height:heightData}">
    <el-row>
    <el-col :span="8" :offset="7" style="margin-top:50px;margin-bottom: 50px">
    <el-card class="box-card">
      <el-tabs v-model="activeName">
        <el-tab-pane label="" name="userRegister">
          <el-row>
            <el-form :model="userForm" :rules="registerRule" ref="userForm" label-width="100px">
              <el-form-item label="" prop="username">
                <el-input v-model="userForm.username" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="passwd">
                <el-input v-model="userForm.passwd" type="password" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="email">
                <el-input v-model="userForm.email" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="userForm.address" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="nickname">
                <el-input v-model="userForm.nickname" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="userForm.userCode" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="phone">
                <el-input v-model="userForm.phone" placeholder=""></el-input>
              </el-form-item>
              <!--<el-form-item label="">
                <el-input v-model="userForm.verificationCode" placeholder=""></el-input>
              </el-form-item>-->
            </el-form>
            <el-row><el-col :span="13" :offset="5">
              <el-button type="primary" @click="userRegister('userForm')"></el-button>
            </el-col>
            </el-row>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="" name="enterpriseRegister">
          <el-row>
            <el-form :model="enterpriseForm" :rules="enterpriseRule" ref="enterpriseForm" label-width="120px">
              <el-form-item label="" prop="username">
                <el-input v-model="enterpriseForm.username" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="passwd">
                <el-input v-model="enterpriseForm.passwd" type="password" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="email">
                <el-input v-model="enterpriseForm.email" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="enterpriseForm.name" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="nickname">
                <el-input v-model="enterpriseForm.nickname" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="orgId">
                <el-input v-model="enterpriseForm.orgId" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="enterpriseForm.addr" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="phone">
                <el-input v-model="enterpriseForm.phone" placeholder=""></el-input>
              </el-form-item>
              <!--<el-form-item label="">
                <el-input v-model="enterpriseForm.verificationCode" placeholder=""></el-input>
              </el-form-item>-->
            </el-form>
            <el-row><el-col :span="13" :offset="5">
              <el-button type="primary" @click="enterpriseRegister('enterpriseForm')"></el-button>
            </el-col>
            </el-row>
          </el-row>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    </el-col>
    </el-row>
  </div>
</template>

<script>
    export default {
        name: "signPage",
      data(){
        let checkUsername = (rule, value, callback) => {
          if (!value) {
            return callback(new Error(''));
          } else {
            const reg = /^[A-Za-z0-9]+$/;
            if (reg.test(value)) {
              callback();
            } else {
              return callback(new Error(''));
            }
          }
        };
        let checkPwd = (rule, value, callback) => {
          if (!value) {
            return callback(new Error(''));
          } else {
            const reg = /^(\w){6,20}$/;
            if (reg.test(value)) {
              callback();
            } else {
              return callback(new Error('6-20'));
            }
          }
        };
        let checkPhone = (rule, value, callback) => {
          if (!value) {
            return callback(new Error(''));
          } else {
            const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
            console.log(reg.test(value));
            if (reg.test(value)) {
              callback();
            } else {
              return callback(new Error(''));
            }
          }
        };

          return {
            activeName: "userRegister",
            userForm:{
              username:"",
              passwd:"",
              email:"",
              address:"",
              nickname:"",
              userCode:"",
              phone:"",
              verificationCode:""
            },
            heightData:0,
            enterpriseForm:{
              username:"",
              passwd:"",
              email:"",
              name:"",
              addr:"",
              nickname:"",
              orgId:"",
              phone:"",
              verificationCode:""
            },
            registerRule: {
              username: [{required: true, message: '', trigger: 'blur'},
                {validator: checkUsername,trigger: 'blur'}],
              passwd: [{required: true, message: '', trigger: 'blur'},
                {validator: checkPwd,trigger: 'blur'}],
              email: [
                {required: true, message: '', trigger: 'blur'},
                { type: 'email', message: '', trigger: ['blur', 'change'] }
                ],
              address: [{required: true, message: '', trigger: 'blur'}],
              nickname: [{required: true, message: '', trigger: 'blur'}],
              userCode: [{required: true, message: '', trigger: 'blur'}],
              phone: [{required: true, message: '', trigger: 'blur'},
                {validator: checkPhone,trigger: 'blur'}],
        },
            enterpriseRule: {
              username: [{required: true, message: '', trigger: 'blur'},
                {validator: checkUsername,trigger: 'blur'}],
              passwd: [{required: true, message: '', trigger: 'blur'},
                {validator: checkPwd,trigger: 'blur'}],
              email: [
                {required: true, message: '', trigger: 'blur'},
                { type: 'email', message: '', trigger: ['blur', 'change'] }
              ],
              address: [{required: true, message: '', trigger: 'blur'}],
              nickname: [{required: true, message: '', trigger: 'blur'}],
              orgId: [{required: true, message: '', trigger: 'blur'}],
              phone: [{required: true, message: '', trigger: 'blur'},
                {validator: checkPhone,trigger: 'blur'}],
            }
          }
      },
       mounted(){
          this.resize();
        window.addEventListener('resize', this.resize);
        },
      beforeDestroy() {
        window.removeEventListener('resize', this.resize);
      },
      methods:{
        resize() {
          const r = Math.max(document.documentElement.clientHeight, 768);
          this.heightData = r - 120 + 'px';
         },
        userRegister(formName){
          /*this.$refs[formName].validate((valid) => {
            if (valid) {*/
              //username
              this.$axios.get(baseUrl+'/sysuser/check-username',
                {params:{
                    username:this.userForm.username
                  }
                }).then((res)=>{
                if(res.data.data==="CHECK_NO_EXIST"){
                  //email
                  this.$axios.get(baseUrl+'/sysuser/check',
                    {params:{
                        column:"email",
                        value:this.userForm.email
                      }
                    }).then((res)=>{
                    if(res.data.data==="CHECK_NO_EXIST"){
                      //phone
                      this.$axios.get(baseUrl+'/sysuser/check',
                        {params:{
                            column:"phone",
                            value:this.userForm.phone
                          }
                        }).then((res)=>{
                        if(res.data.data==="CHECK_NO_EXIST"){
                          //
                          this.$axios.post(baseUrl+'/sysuser/create',this.userForm).then((res)=>{
                            if(res.data.message===""){
                              this.$message({
                                showClose: true,
                                message: "",
                                type: 'success'
                              });
                              this.$router.push('/');
                            }else{
                              this.$message({
                                showClose: true,
                                message: "",
                                type: 'error'
                              });
                            }
                          }).catch((err)=>{
                            console.log(err);
                          });
                        }else{
                          this.$message({
                            showClose: true,
                            message: "",
                            type: 'error'
                          });
                        }
                      }).catch((err)=>{
                        console.log(err);
                      });
                    }else{
                      this.$message({
                        showClose: true,
                        message: "",
                        type: 'error'
                      });
                    }
                  }).catch((err)=>{
                    console.log(err);
                  });
                }else{
                  this.$message({
                    showClose: true,
                    message: "",
                    type: 'error'
                  });
                }
              }).catch((err)=>{
                console.log(err);
              });
           /* } else {
              console.log('error submit!!');
              return false;
            }
          });*/
        },
        enterpriseRegister(formName){
          this.$refs[formName].validate((valid) => {
            if (valid) {
              //username
              this.$axios.get(baseUrl+'/sysuser/check-username',
                {params:{
                    username:this.enterpriseForm.username
                  }
                }).then((res)=>{
                if(res.data.data==="CHECK_NO_EXIST"){
                  //email
                  this.$axios.get(baseUrl+'/sysuser/check',
                    {params:{
                        column:"email",
                        value:this.enterpriseForm.email
                      }
                    }).then((res)=>{
                    if(res.data.data==="CHECK_NO_EXIST"){
                      //phone
                      this.$axios.get(baseUrl+'/sysuser/check',
                        {params:{
                            column:"phone",
                            value:this.enterpriseForm.phone
                          }
                        }).then((res)=>{
                        if(res.data.data==="CHECK_NO_EXIST"){
                          //
                          this.$axios.post(baseUrl+'/sysuser/org-reg',this.enterpriseForm).then((res)=>{
                            if(res.data.message===""){
                              this.$message({
                                showClose: true,
                                message: "",
                                type: 'success'
                              });
                              this.$router.push('/');
                            }else{
                              this.$message({
                                showClose: true,
                                message: "",
                                type: 'error'
                              });
                            }
                          }).catch((err)=>{
                            console.log(err);
                          });
                        }else{
                          this.$message({
                            showClose: true,
                            message: "",
                            type: 'error'
                          });
                        }
                      }).catch((err)=>{
                        console.log(err);
                      });
                    }else{
                      this.$message({
                        showClose: true,
                        message: "",
                        type: 'error'
                      });
                    }
                  }).catch((err)=>{
                    console.log(err);
                  });
                }else{
                  this.$message({
                    showClose: true,
                    message: "",
                    type: 'error'
                  });
                }
              }).catch((err)=>{
                console.log(err);
              });
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        }
      }
    }
</script>

<style scoped>
  .register-page{
    width:100%;
    height:100%;
    background-size:100% 100%!important;
    background: url("../assets/img/login_bg.png") no-repeat;
  }
  .box-card{background-color: #ffffff}
  .el-row button{width: 100%;margin: 10px 0px}
  .managerArea{display: none}
</style>
<style>

</style>



