<!---->
<template>
  <div id="applicationManage">
    <el-row>
      <el-form :inline="true" :model="searchItem" ref="searchItem">
        <el-form-item label="">
          <el-input v-model="searchItem.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="searchItem.type" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="searchItem.status" :placeholder="statusArr.title">
            <el-option
              v-for="item in statusArr.options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search(1)"></el-button>
          <el-button type="default" @click="reset('searchItem')"></el-button>
        </el-form-item>
      </el-form>
    </el-row>
    <el-row>
      <el-col :span="22">
        <div>
          <el-table
            :data="tableData"
            border
            size="medium"
            class="applicationTable"
            @selection-change="getRowDatas"
          >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column type="index" width="50" label=""></el-table-column>
            <el-table-column prop="appName" width="120" label="">
              <template slot-scope="scope">
                <el-button
                  @click="detailRow(scope.row)"
                  type="text"
                  size="medium"
                >{{scope.row.name}}</el-button>
              </template>
            </el-table-column>
            <el-table-column prop="appType" label=""></el-table-column>
            <el-table-column prop="vendor" width="150" label=""></el-table-column>
            <el-table-column prop="version" label=""></el-table-column>
            <el-table-column prop="appReleaseTime" width="120" label=""></el-table-column>
            <el-table-column prop="containerType" width="120" label=""></el-table-column>
            <el-table-column prop="containerVersion" label=""></el-table-column>
            <el-table-column prop="options" label="">
              <template slot-scope="scope">
                <el-button @click="editRow(scope.row)" type="text" size="medium"></el-button>
                <el-button @click="delRow(scope.row)" type="text" size="medium"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-row style="margin:20px 0px">
            <el-button type="primary" round><i class="el-icon-upload"></i></el-button>
            <el-pagination
              background
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="1"
              :page-sizes="[5, 10,15]"
              :page-size="tableLimit"
              layout="total, sizes, prev, pager, next"
              :total="tableSize"
              style="float: right"
            ></el-pagination>
          </el-row>
        </div>
      </el-col>
    </el-row>
    <!-- / -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible">
      <el-form
        :model="dialogForm"
        :rules="dialogRules"
        ref="dialogForm"
        label-width="100px"
        class="acForm"
      >
        <el-col :span="24">
          <el-form-item label="" prop="name">
            <span v-if="ifDialogDetail">{{dialogForm.name}}</span>
            <el-input v-if="!ifDialogDetail" v-model="dialogForm.name" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="" prop="type">
            <span v-if="ifDialogDetail">{{dialogForm.type}}</span>
            <el-select
              v-if="!ifDialogDetail"
              v-model="dialogForm.type"
              :placeholder="appTypeArr.title"
              style="width:100%"
            >
              <el-option
                v-for="item in appTypeArr.options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="" prop="factory">
            <span v-if="ifDialogDetail">{{dialogForm.factory}}</span>
            <el-input v-if="!ifDialogDetail" v-model="dialogForm.factory" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="" prop="version">
            <span v-if="ifDialogDetail">{{dialogForm.version}}</span>
            <el-input v-if="!ifDialogDetail" v-model="dialogForm.version" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="" prop="containerType">
            <span v-if="ifDialogDetail">{{dialogForm.containerType}}</span>
            <el-select
              v-if="!ifDialogDetail"
              v-model="dialogForm.containerType"
              placeholder=""
              style="width:100%"
            >
              <el-option
                v-for="item in containerTypeArr.options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="" prop="containerVersion">
            <span v-if="ifDialogDetail">{{dialogForm.containerVersion}}</span>
            <el-input
              v-if="!ifDialogDetail"
              v-model="dialogForm.containerVersion"
              placeholder=""
            ></el-input>
          </el-form-item>
        </el-col>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm('dialogForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "applicationManage",
  data() {
    return {
      tableSize: 0,
      tableLimit: 10,
      selectedRow: [],
      searchItem: {
        name: "",
        type: "",
        status: ""
      },
      statusArr: {
        title: "",
        options: [
          {
            value: "1",
            label: ""
          },
          {
            value: "0",
            label: ""
          }
        ]
      },
      appTypeArr: {
        title: "",
        options: [
          {
            value: "1",
            label: "1"
          },
          {
            value: "2",
            label: "2"
          },
          {
            value: "3",
            label: "3"
          }
        ]
      },
      containerTypeArr: {
        title: "",
        options: [
          {
            value: "1",
            label: "1"
          },
          {
            value: "2",
            label: "2"
          },
          {
            value: "3",
            label: "3"
          }
        ]
      },
      tableData: [],
      dialogTitle: "",
      dialogFormVisible: false,
      ifDialogDetail: false,
      dialogForm: {
        name: "",
        type: "",
        factory: "",
        version: "",
        containerType: "",
        containerVersion: ""
      },
      dialogRules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        type: [
          { required: true, message: "", trigger: "change" }
        ],
        factory: [
          { required: true, message: "", trigger: "change" }
        ],
        version: [
          { required: true, message: "", trigger: "change" }
        ],
        containerType: [
          { required: true, message: "", trigger: "change" }
        ],
        containerVersion: [
          { required: true, message: "", trigger: "change" }
        ]
      }
    };
  },
  mounted() {
    this.search(1);
  },
  methods: {
    search(page) {
      this.$axios.post(baseUrl+'/admin/app/files?'+"pageIndex="+page+"&pageSize="+this.tableLimit,{appInfo:this.searchItem},
      {headers: {"Content-Type": "application/json"}}
      ).then((res)=>{
        this.tableSize=res.data.data.total;
        this.tableData=res.data.data.records;
      }).catch((err)=>{
        console.log(err);
      });
    },
    reset(formName) {
      this.$refs[formName].resetFields();
    },
    getRowDatas(row) {
      this.selectedRow = row;
      console.info(row);
    },
    add(formName) {
      this.dialogTitle = "";
      this.ifDialogDetail = false;
      this.dialogForm = {};
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs[formName].resetFields();
      });
    },
    detailRow(row) {
      this.dialogTitle = "";
      this.ifDialogDetail = true;
      this.dialogForm = Object.assign({},row);
      this.dialogFormVisible = true;
    },
    editRow(row) {
      this.dialogTitle = "";
      this.dialogForm = Object.assign({},row);
      this.ifDialogDetail = false;
      this.dialogFormVisible = true;
    },
    delRow(row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          // this.$axios.post('',{
          //   id:row.id,'
          // }).then((res)=>{
          //   this.search(page);
          // }).catch((err)=>{
          //   console.log(err);
          // });
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // this.$axios.post('',{
          //   dialogForm,
          // }).then((res)=>{
          this.dialogFormVisible = false;
          this.$message({
            message: "",
            type: "success"
          });
          this.$refs[formName].resetFields();
          //   this.search(page);
          // }).catch((err)=>{
          //   console.log(err);
          // });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    handleSizeChange(val) {
      this.tableLimit = val;
      this.search(1);
    },
    handleCurrentChange(val) {
      this.search(val);
    }
  }
};
</script>

<style scoped>
.applicationTable {
  max-height: 500px;
  overflow: auto;
}
</style>



