<!---->
<template>
  <div id="userManage">
    <el-row>
      <el-form :inline="true" :model="searchItem" ref="searchItem">
        <el-form-item label="">
          <el-input v-model="searchItem.userName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="ID">
          <el-input v-model="searchItem.tenantId" placeholder="ID"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search(1)"></el-button>
          <el-button type="default" @click="reset('searchItem')"></el-button>
        </el-form-item>
      </el-form>
    </el-row>
    <el-row>
      <el-col :span="22">
        <div>
          <el-table
            :data="tableData"
            border
            size="medium"
            class="userTable"
            @selection-change="getRowDatas"
          >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column type="index" width="50" label=""></el-table-column>
            <el-table-column prop="uid" width="120" label="ID" v-if="false"></el-table-column>
            <el-table-column prop="username" width="120" label=""></el-table-column>
            <el-table-column prop="usertype" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.usertype===1"></span>
                <span v-if="scope.row.usertype===0"></span>
              </template>
            </el-table-column>
            <el-table-column prop="tenantid" label=""></el-table-column>
            <el-table-column prop="status" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.status===1" style="color:#67c23a"></span>
                <span v-if="scope.row.status===8" style="color:gray"></span>
                <span v-if="scope.row.status===9" style="color:red"></span>
              </template>
            </el-table-column>
            <el-table-column prop="options" width="250" label="">
              <template slot-scope="scope">
                <el-button @click="disableUser(scope.row)" type="text" size="medium"></el-button>
                <el-button @click="editRow(scope.row)" type="text" size="medium"></el-button>
                <el-button @click="delRow(scope.row)" type="text" size="medium"></el-button>
                <el-button @click="resetPwd(scope.row)" type="text" size="medium"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-row style="margin:20px 0px">
            <el-button-group>
              <el-button
                type="success"
                round
                size="small"
                icon="el-icon-refresh"
                @click="search(1,10)"
              ></el-button>
              <el-button
                type="primary"
                round
                size="small"
                icon="el-icon-plus"
                @click="add('dialogForm')"
              ></el-button>
              <!-- <el-button type="warning" round size="small" icon="el-icon-edit" @click="editRow()"></el-button>
              <el-button type="danger" round size="small" icon="el-icon-delete" @click="delRow()"></el-button>-->
            </el-button-group>
            <el-pagination
              background
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="1"
              :page-sizes="[5, 10,15]"
              :page-size="tableLimit"
              layout="total, sizes, prev, pager, next"
              :total="tableSize"
              style="float: right"
            ></el-pagination>
          </el-row>
        </div>
      </el-col>
    </el-row>
    <!-- / -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible">
      <el-form :model="dialogForm" :rules="dialogRules" ref="dialogForm" label-width="100px">
        <el-form-item label=":" prop="username">
          <el-col :span="15">
            <el-input v-if="!ifUpdate" v-model="dialogForm.username" placeholder=""></el-input>
            <span v-if="ifUpdate">{{dialogForm.username}}</span>
          </el-col>
        </el-form-item>
        <el-form-item label=":" prop="passwd">
          <el-col :span="15">
            <el-input v-model="dialogForm.passwd" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="ID:" prop="tenantid">
          <el-col :span="15">
            <el-input v-model="dialogForm.tenantid" placeholder="ID"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="usertype">
          <el-col :span="15">
            <el-radio v-model="dialogForm.usertype" label="0"></el-radio>
            <el-radio v-model="dialogForm.usertype" label="1"></el-radio>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-radio v-model="dialogForm.status" label="1"></el-radio>
          <el-radio v-model="dialogForm.status" label="8"></el-radio>
          <el-radio v-model="dialogForm.status" label="9"></el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm('dialogForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "userManage",
  data() {
    return {
      tableSize: 0,
      tableLimit: 10,
      selectedRow: [],
      searchItem: {
        userName: "",
        tenantId: ""
      },
      orgArr: {
        title: "",
        options: [
          {
            value: "1",
            label: ""
          },
          {
            value: "2",
            label: ""
          }
        ]
      },
      tenantArr: {
        title: "",
        options: [
          {
            value: "1",
            label: ""
          },
          {
            value: "2",
            label: ""
          }
        ]
      },
      tableData: [],
      dialogTitle: "",
      dialogFormVisible: false,
      dialogForm: {
        username: "",
        passwd: "",
        tenantid: "",
        usertype: "",
        status: ""
      },
      dialogRules: {
        username: [
          { required: true, message: "", trigger: "blur" }
        ],
        passwd: [
          { required: true, message: "", trigger: "change" }
        ],
        tenantid: [
          { required: true, message: "ID", trigger: "change" }
        ],
        usertype: [
          { required: true, message: "", trigger: "change" }
        ],
        status: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      ifUpdate: false
    };
  },
  mounted() {
    this.search(1);
  },
  methods: {
    search(page) {
      let param = {};
      param.pageSize = this.tableLimit;
      param.pageIndex = page;
      if (this.searchItem.userName !== "")
        param.userName = this.searchItem.userName;
      if (this.searchItem.tenantId !== "")
        param.tenantId = this.searchItem.tenantId;
      this.$axios
        .get(baseUrl+"/admin/user/list?" + this.$qs.stringify(param))
        .then(res => {
          this.tableSize = res.data.data.total;
          this.tableData = res.data.data.records;
        })
        .catch(err => {
          console.log(err);
        });
    },
    reset(formName) {
      this.$nextTick(() => {
        this.$refs[formName].resetFields();
      });
    },
    getRowDatas(row) {
      this.selectedRow = row;
      console.info(row);
    },
    add(formName) {
      this.dialogTitle = "";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs[formName].resetFields();
      });
    },
    editRow(row) {
      this.dialogTitle = "";
      this.dialogForm = Object.assign({},row);
      this.ifUpdate = true;
      this.dialogFormVisible = true;
    },
    delRow(row) {
      console.info(row);
      this.$confirm("?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            this.$axios
              .delete(
                baseUrl+"/admin/user/remove?userId=" + row.uid
              )
              .then(res => {
                this.$message({
                  type: "success",
                  message: "!"
                });
                this.search(1);
              })
              .catch(err => {
                console.log(err);
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
    },
    //
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          //
          if (this.dialogTitle.indexOf("") > -1) {
            this.$axios
              .post(baseUrl+"/admin/user/save?"+this.$qs.stringify(this.dialogForm))
              .then(res => {
                this.dialogFormVisible = false;
                this.$message({
                  message: "",
                  type: "success"
                });
                this.$refs[formName].resetFields();
                this.search(1);
              })
              .catch(err => {
                console.log(err);
              });
          }else{//
          let param={};
            Object.keys(this.dialogForm).forEach(item => {
              if(item!=="createTime"&&item!=="deleteTime") param[item]=this.dialogForm[item];
            });
            this.$axios
              .put(baseUrl+"/admin/user/update?"+this.$qs.stringify(param))
              .then(res => {
                this.dialogFormVisible = false;
                this.$message({
                  message: "",
                  type: "success"
                });
                this.$refs[formName].resetFields();
                this.search(1);
              })
              .catch(err => {
                console.log(err);
              });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //
    resetPwd(row) {},
    //
    disableUser(row) {},
    handleSizeChange(val) {
      this.tableLimit = val;
      this.search(1);
    },
    handleCurrentChange(val) {
      this.search(val);
    }
  }
};
</script>

<style scoped>
.userTable {
  max-height: 500px;
  overflow: auto;
}
</style>



